import {Howl, Howler} from 'howler';


export var music = {
	sound: null,
	init()
	{

		if(!this.sound)
			this.sound = new Howl({
				src: [`/musicBackground/${getRandomInt(19)}.mp3`],
				volume: 0.2,
				loop: true,
				autoplay: true,
				html5:true,
			});
	},
	mute()
	{
		console.log(123);
	}
}


function getRandomInt(max) {
  return Math.floor(Math.random() * max);
}
